  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0; }
  100% {
    visibility: hidden;
    opacity: 0; } }

